@import url(../js/libs/slick-carousel/slick/slick.css);
@font-face {
  font-family: "PFDinTextPro";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.eot");
  src: url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.woff") format("woff"), url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "PFDinTextPro";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.eot");
  src: url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.woff") format("woff"), url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "PFDinTextPro";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.eot");
  src: url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.woff") format("woff"), url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.ttf") format("truetype");
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: PFDinTextPro;
  font-weight: 300;
  line-height: 1.37;
}

.block-top {
  background-color: #3d4c62;
  position: relative;
  padding: 174px 0 173px;
  overflow: hidden;
}

.block-top__img {
  width: 347px;
  height: 347px;
  margin: 0 auto;
}

.block-top__img img {
  display: block;
  max-width: 100%;
}

.block-top__user {
  position: absolute;
  top: 80px;
  right: 100px;
}

.block-top__title {
  color: #ffffff;
  font-size: 60px;
  font-weight: 300;
  text-align: center;
  margin-top: 64px;
  text-transform: uppercase;
}

.block-top__title-error {
  font-size: 300px;
  text-align: center;
  font-weight: 500;
  color: #ffffff;
  animation: neon 0.01s ease-in-out infinite alternate;
}

@keyframes neon {
  0% {
    text-shadow: 0 0 6px rgba(202, 228, 225, 0.92), 0 0 30px rgba(202, 228, 225, 0.34), 0 0 12px rgba(1, 2, 1, 0.52), 0 0 21px rgba(1, 2, 1, 0.92), 0 0 34px rgba(1, 2, 1, 0.78), 0 0 54px rgba(1, 2, 1, 0.98);
  }
  100% {
    text-shadow: 0 0 6px rgba(202, 228, 225, 0.98), 0 0 30px rgba(202, 228, 225, 0.42), 0 0 12px rgba(1, 2, 1, 0.58), 0 0 22px rgba(1, 2, 1, 0.84), 0 0 38px rgba(1, 2, 1, 0.88), 0 0 60px #010201;
  }
}

.form-user__row {
  margin-bottom: 10px;
}

.form-user__label {
  display: block;
  margin-bottom: 10px;
  color: #3d4c62;
}

.form-user__input {
  border-radius: 5px;
  border: 2px solid #3d4c62;
  height: 30px;
  width: 100%;
}

.form-user__btn-big {
  text-align: right;
  margin-bottom: 8px;
}

.form-user__btn {
  margin: 0 auto;
  text-align: center;
}

.form-user__user-in {
  border-radius: 10px;
  background-color: #ffffff;
  padding: 20px;
  width: 253px;
  opacity: 0;
  visibility: hidden;
  position: relative;
  right: -300px;
  transition: all 0.5s;
}

.form-user__user-in.open {
  opacity: 1;
  visibility: visible;
  right: 0;
}

.button-in {
  border-radius: 5px;
  border: 2px solid #3d4c62;
  background-color: #3d4c62;
  color: #ffffff;
  font-size: 16px;
  font-weight: 300;
  padding: 8px 33px;
  cursor: pointer;
  text-transform: uppercase;
  transition: opacity 0.3s;
}

.button-in:hover {
  opacity: 0.9;
}

.button-in-big {
  border-radius: 23px;
  border: 2px solid #ffffff;
  padding: 7px 44px;
  background-color: transparent;
  font-size: 24px;
  font-weight: 300;
  color: #ffffff;
  cursor: pointer;
  font-family: PFDinTextPro;
  transition: border-radius 0.3s;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
}

.button-in-big:hover {
  border-radius: 15px;
}

.map-block {
  height: 800px;
  position: relative;
}

.map-block__img {
  height: 100%;
}

.map-block__button {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
}

.map-block__btn {
  min-width: 100px;
  padding: 10px 0;
  border-radius: 0 0 2px;
  border: 1px solid #3d4c62;
  background-color: #ffffff;
  color: #3d4c62;
  font-size: 16px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  text-align: center;
  transition: all 0.5s;
  text-decoration: none;
}

.map-block__btn:before {
  content: '';
  position: absolute;
  bottom: -3px;
  height: 5px;
  left: 0;
  width: 0;
  background-color: transparent;
  transition: all 0.5s;
}

.map-block__btn:hover, .map-block__btn.is-active {
  font-weight: 500;
}

.map-block__btn:hover:before, .map-block__btn.is-active:before {
  content: '';
  position: absolute;
  bottom: -3px;
  height: 5px;
  left: 0;
  width: 100%;
  background-color: #3d4c62;
}

.block {
  font-family: PFDinTextPro;
  font-weight: 300;
  line-height: 1.37;
  display: flex;
  align-items: stretch;
  position: relative;
  height: auto;
  min-height: 100vh;
}

.block__reverse {
  flex-direction: row-reverse;
}

.block__info {
  width: 37%;
  background-color: #3d4c62;
  padding: 40px 100px 60px;
}

.block__img {
  width: 63%;
}

.info {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.info__title {
  font-size: 16px;
  max-width: 164px;
  margin: 0 auto 20px;
  text-align: center;
  font-weight: 500;
  line-height: 1.3;
  font-size: 24px;
}

.info__title .title-big {
  font-size: 24px;
  text-transform: uppercase;
}

.info__title-tablet {
  padding-top: 20px;
}

.info__subtitle {
  margin: 0 auto 27px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
}

.info__block {
  font-weight: 300;
  color: #ffffff;
}

.info__block ul li {
  position: relative;
  padding-left: 10px;
}

.info__block ul li:before {
  content: '';
  position: absolute;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  left: 0;
  background-color: #ffffff;
  display: block;
  top: 9px;
}

.title-gray {
  color: #3d4c62;
}

.title-lg-hide {
  display: none;
}

.xs-show {
  display: none;
}

.img-infra {
  position: relative;
}

.img-infra__text {
  display: inline-block;
  position: absolute;
  z-index: 10;
  font-weight: 400;
  top: 48px;
  left: 0;
  display: inline-block;
  color: #50627e;
  font-size: 16px;
  padding: 11px 14px 7px 18px;
  border-radius: 0 10px 2px 0;
  border: 1px solid #ffffff;
  background-color: #ffffff;
}

.img-infra__text_reverse {
  right: 0;
  left: auto;
  border-radius: 10px 0 0 2px;
}

.img-infra__location {
  position: absolute;
  z-index: 10;
  top: 94px;
  left: 10px;
  display: inline-block;
  width: 59px;
  height: 59px;
  border: 2px solid #ffffff;
  background-color: #3d4c62;
  border-radius: 50%;
  cursor: pointer;
}

.img-infra__location_reverse {
  right: 10px;
  left: auto;
}

.img-infra__location img {
  display: block;
  max-width: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.img-infra__logo {
  position: absolute;
  z-index: 10;
  top: 47px;
  right: 0;
  display: inline-block;
  background-color: #ffffff;
  width: 366px;
  height: 168px;
  border-radius: 20px 0 0 20px;
}

.img-infra__logo_reverse {
  left: 0;
  right: auto;
  border-radius: 0 20px 20px 0;
}

.img-infra__logo img {
  display: block;
  max-width: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.img-infra__button {
  position: absolute;
  right: 100px;
  bottom: 48px;
  display: flex;
}

.img-infra__button_reverse {
  left: 100px;
  right: auto;
}

.img-infra__images {
  height: 100%;
}

.img-infra__images img {
  display: block;
  width: 100%;
  min-height: 100%;
  object-fit: cover;
}

.map-hide {
  display: none;
}

.btn {
  display: block;
  border-radius: 50%;
  font-size: 12px;
  color: #ffffff;
  width: 120px;
  height: 120px;
  border: 2px solid #ffffff;
  background-color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  position: relative;
}

.btn:nth-child(2) {
  margin-left: 15px;
  margin-right: 15px;
}

.btn__text {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.btn img {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.popap {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  background: rgba(0, 0, 0, 0.95);
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 20;
  margin: 0 auto;
  transition: all 0.3s;
  height: 100%;
}

.popap.open {
  opacity: 1;
  visibility: visible;
}

.popap__inner {
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 0 100px;
}

.popap__in {
  position: relative;
  padding: 0 20px;
  height: 100%;
  max-width: 1640px;
  margin: 0 auto;
}

.popap-map {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  margin: 0 auto;
  transition: all 0.3s;
}

.popap-map.open {
  opacity: 1;
  visibility: visible;
}

.popap-map__inner {
  height: 100%;
}

.popap-map__inner img {
  min-height: 100%;
  display: block;
  object-fit: cover;
  max-width: 100%;
}

.popap-map__bottom {
  background-color: rgba(0, 0, 0, 0.52);
  position: absolute;
  bottom: 0;
  padding: 28px 0;
  left: 0;
  right: 0;
}

.popap-map__google {
  display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 23px;
  border: 2px solid #ffffff;
  font-size: 16px;
  font-weight: 300;
  color: #ffffff;
  max-width: 272px;
  width: 100%;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 0;
  transition: all 0.5s;
}

.popap-map__google:hover {
  opacity: 0.7;
  border-radius: 15px;
}

.popap-map__btn {
  max-width: 1640px;
  padding: 0 40px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.popap-map__icon-text {
  margin-left: 24px;
}

.gallery {
  margin: 0 auto;
  max-width: 928px;
}

.gallery__btn {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 22px 0;
  margin: 0 auto;
  display: flex;
  width: 100%;
  max-width: 1600px;
  justify-content: space-between;
}

.gallery__btn-center {
  width: 272px;
}

.gallery__btn-prev, .gallery__btn-next {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 300;
  color: #ffffff;
  cursor: pointer;
  transition: opacity 0.5s;
}

.gallery__btn-prev:hover, .gallery__btn-next:hover {
  opacity: 0.7;
}

.gallery__btn-prev img {
  margin-right: 16px;
}

.gallery__btn-next img {
  margin-left: 16px;
}

.btn-plan {
  border-radius: 23px;
  border: 2px solid #ffffff;
  text-align: center;
  width: 100%;
  padding: 25px 0;
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 300;
  transition: 0.5s;
}

.btn-plan:hover {
  border-radius: 15px;
}

.btn-close {
  width: 48px;
  height: 48px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  position: absolute;
  top: 26px;
  right: 0;
}

.btn-close img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.btn-close-map {
  width: 48px;
  height: 48px;
  border: 1px solid #fff;
  border-radius: 50%;
  position: relative;
  display: inline-block;
}

.btn-close-map img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.popap .slick-slide img {
  height: 80vh;
  margin: 0 auto;
  width: 100%;
  object-fit: cover;
}

.footer {
  font-size: 16px;
  font-weight: 300;
}

.footer__inner {
  padding: 32px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__logo {
  display: flex;
  text-decoration: none;
  align-items: center;
  color: #3d4c62;
}

.footer__logo-text {
  font-weight: 500;
  margin: 0 16px 0 5px;
}

.footer__logo-dev {
  text-decoration: none;
}

.footer__logo-dev-text {
  display: block;
  color: #3d4c62;
}

.footer__icons-soc {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.footer__icon-soc {
  margin: 0 8px;
  display: inline-block;
}

@media only screen and (max-width: 1200px) {
  .block-top__title {
    font-size: 48px;
  }
  .block-top__user {
    top: 32px;
    right: 32px;
  }
  .block {
    flex-direction: column-reverse;
    min-height: 100px;
  }
  .block__info {
    width: auto;
    padding: 20px 29px;
  }
  .block__img {
    width: 100%;
  }
  .img-infra__text {
    top: 23px;
  }
  .img-infra__logo {
    width: 188px;
    height: 80px;
    top: 23px;
  }
  .img-infra__logo img {
    max-width: 134px;
    max-height: 60px;
  }
  .img-infra__button {
    right: 32px;
    bottom: 24px;
  }
  .img-infra__location {
    top: 75px;
  }
  .img-infra__text_reverse {
    left: 0;
    right: auto;
    border-radius: 0 10px 2px 0;
  }
  .img-infra__location_reverse {
    right: auto;
    left: 32px;
  }
  .img-infra__logo_reverse {
    right: 0;
    left: auto;
    border-radius: 20px 0 0 20px;
  }
  .img-infra__button_reverse {
    right: 32px;
    left: auto;
  }
  .info__subtitle {
    margin: 0 auto 18px;
  }
  .info__description {
    margin-bottom: 18px;
  }
  .gallery__btn {
    top: 0;
    bottom: auto;
    background-color: rgba(0, 0, 0, 0.52);
  }
  .gallery__btn-prev, .gallery__btn-next {
    display: none;
  }
  .popap__in {
    padding: 0;
  }
  .popap__inner {
    padding: 0 30px;
  }
  .popap .slick-slide img {
    height: 100vh;
  }
  .popap .slick-dots {
    position: absolute;
    bottom: 25px;
    left: 0;
    text-align: center;
    right: 0;
    display: flex;
    justify-content: center;
  }
  .popap .slick-dots li {
    margin: 0 8px;
    border-radius: 50%;
    background: #ffffff;
    width: 10px;
    height: 10px;
    border: none;
  }
  .popap .slick-dots li button {
    opacity: 0;
  }
  .title-lg-hide {
    display: block;
  }
  .title-lg-show {
    display: none;
  }
  .btn-plan {
    padding: 13px 0;
  }
  .footer__inner {
    flex-direction: column;
    justify-content: center;
  }
  .footer__logo {
    flex-direction: column;
  }
  .footer__logo-text {
    margin: 10px 0;
  }
  .footer__icons-soc {
    position: relative;
    left: auto;
    transform: translateX(0);
    margin: 15px 0;
  }
}

.gm-style-iw {
  overflow: visible;
  border-radius: 20%;
  left: 50px;
}

.map__info-marker {
  font-size: 14px;
  font-weight: 500;
  font-family: PFDinTextPro;
  text-align: center;
  padding: 50px;
  box-shadow: 1px 2px 9px 1px rgba(1, 2, 1, 0.19);
  width: 166px;
  color: #3d4c62;
  box-sizing: border-box;
  padding: 10px 0 10px 20px;
}

.map__info-marker .big-text {
  text-transform: uppercase;
  font-size: 16px;
}

.map__info-marker a {
  display: block;
  margin-top: 5px;
}

@media only screen and (max-width: 768px) {
  .block-top__img {
    width: 174px;
    height: 174px;
  }
  .block-top__title {
    font-size: 24px;
  }
  .block-top__user {
    right: 15px;
  }
  .block-top__title-error {
    font-size: 150px;
  }
  .block__info {
    padding: 20px 15px;
  }
  .map-block__button {
    right: 0;
  }
  .map-block__btn {
    width: 50%;
  }
  .button-in-big {
    font-size: 16px;
    padding: 5px 24px;
  }
  .img-infra__logo {
    width: 113px;
    height: 52px;
    border-radius: 10px 0 0 10px;
    top: 16px;
  }
  .img-infra__logo img {
    max-width: 69px;
    max-height: 44px;
  }
  .img-infra__text {
    padding: 5px 10px 4px 12px;
    top: 16px;
  }
  .img-infra__button {
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    bottom: 16px;
  }
  .img-infra__location {
    display: none;
    position: relative;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .gallery__btn {
    background-color: transparent;
  }
  .gallery__btn-center {
    display: none;
  }
  .popap__inner {
    padding: 0 15px;
  }
  .popap-map__btn {
    padding: 0 20px;
  }
  .popap-map__icon-text {
    margin-left: 5px;
  }
  .popap-map__google {
    max-width: 203px;
    padding: 6px 0;
  }
  .popap-map__google img {
    width: 24px;
    height: 24px;
  }
  .btn-close-map,
  .btn-close {
    width: 40px;
    height: 40px;
  }
  .footer__inner {
    padding: 20px 30px;
  }
  .footer__logo-text {
    margin: 8px 0;
  }
  .footer__icons-soc {
    margin: 8px 0;
  }
  .xs-show {
    display: block;
  }
  .xs-hide {
    display: none;
  }
  .btn {
    width: 59px;
    height: 59px;
  }
  .map-hide {
    display: block;
  }
}
